d a much deserved ramen lunch next door.\n      After sleeping off the meal, we were ready to head out for  drinks at L'EscaMoteur Bar but first we stopped\n      into a mother-daughter\n      "),_c('MoreInfo',{attrs:{"keyword":"Okonomiyaki"}},[_vm._v("\n        Okonomiyaki, roughly meaning, “grill what you like” is a hodgepodge dish of ingredients packed\n        into a pancake that is famous in the Osaka and Kyoto area of Japan. Needless to say, ours was excellent.\n      ")]),_vm._v("\n      place for dinner. The bar, however, was the real highlight of the night. This place was like an art installation\n      turned into a bar and was lead by a french man playing the role of a magician. The room was dimly lit and an old\n      couch sat lopsided in the rafters. Beakers and decanters lined the shelves and the cabinets were filled with\n      various liquors. Pages of books covered the light fixtures (fire hazard??)\n      and the bathroom was hidden behind a bookshelf. We tried a few of the drinks but the magic trick drinks stood out\n      to us as the most exciting and delicious (see the video to the right)!\n    ")],1)]),_vm._v(" "),_c('div',{staticClass:"clear"}),_vm._v(" "),_c('div',{staticClass:"col-4"},[_c('ImageCard',{attrs:{"src":"assets/blog1/okonomiyaki.jpg"}},[_vm._v("\n      Okonomiyaki at the mother-daughter shop (PC: Kimberly Whitney)\n    ")])],1),_vm._v(" "),_c('div',{staticClass:"col-4"},[_c('ImageCard',{attrs:{"src":"assets/blog1/fushimi_inari_posts.jpg"}},[_vm._v("\n      Gates lining the path to the summit of Mount Inari (PC: Kimberly Whitney)\n    ")])],1),_vm._v(" "),_c('div',{staticClass:"col-4"},[_c('ImageCard',{attrs:{"src":"assets/blog1/the_conjurer.jpg"}},[_vm._v("\n      L'EscaMoteur Bar in Kyoto\n    ")])],1),_vm._v(" "),_c('div',{staticClass:"clear"}),_vm._v(" "),_c('div',{staticClass:"blog-post col-12"},[_c('p',[_vm._v("In the morning we set out for Mount Fuji which was a hell of a travel day. You would expect getting there to be\n 